/*---------------shadows---------------*/

.effect {
  position: relative;
}

.effect:before,
.effect:after {
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 14px;
  left: 10px;
  width: 50%;
  top: 74%;
  max-width: 358px;
  background: #777;
  transform: rotate(-1deg);
  -webkit-box-shadow: 0 15px 10px #777;
  -moz-box-shadow: 0 15px 10px #777;
  box-shadow: 0 15px 10px #777;
  -webkit-transform: rotate(-1deg);
  -moz-transform: rotate(-1deg);
  -o-transform: rotate(-1deg);
  -ms-transform: rotate(-1deg);
}

.effect:after {
  right: 10px;
  left: auto;
  transform: rotate(1deg);
  -webkit-transform: rotate(1deg);
  -moz-transform: rotate(1deg);
  -o-transform: rotate(1deg);
  -ms-transform: rotate(1deg);
}

.effect0 {
  position: relative;
}

.effect0:before,
.effect0:after {
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 15px;
  left: 10px;
  width: 47%;
  top: 7%;
  max-width: 358px;
  background: #777;
  transform: rotate(-1deg);
  -webkit-box-shadow: 0 15px 10px #777;
  -moz-box-shadow: 0 15px 10px #777;
  box-shadow: 0 15px 10px #777;
  -webkit-transform: rotate(-1deg);
  -moz-transform: rotate(-1deg);
  -o-transform: rotate(-1deg);
  -ms-transform: rotate(-1deg);
}

.effect0:after {
  right: 10px;
  left: auto;
  transform: rotate(1deg);
  -webkit-transform: rotate(1deg);
  -moz-transform: rotate(1deg);
  -o-transform: rotate(1deg);
  -ms-transform: rotate(1deg);
}

